/* eslint no-magic-numbers: 0 */
import React, {Component} from 'react';
import { DashPdfComponents } from '../lib';

class App extends Component {

    componentDidMount() {
        this.loadJS();
    }

    loadJS = () => {
        ////// load the embed API script
        const url = "https://documentcloud.adobe.com/view-sdk/main.js";
        const script = document.createElement("script");
        script.src = url;
        script.async = true;
        document.body.appendChild(script);
    }

    constructor() {
        super();
        this.state = {
            value: ''
        };
        this.setProps.key = "https://documentcloud.adobe.com/view-sdk-demo/PDFs/Bodea Brochure.pdf";
        this.setProps.id = 'pdf-view';
        this.setProps.label = 'pdf-view-label';
        this.setProps.apiKey = '899d52477b7d4b589f808242e8d36cc3';
        this.setProps.fileUrl = "https://documentcloud.adobe.com/view-sdk-demo/PDFs/Bodea Brochure.pdf";
        this.setProps.fileName = "sample pdf.pdf";
        this.setProps.fileId = '0d07d124-ac85-43b3-a867-36930f502ac6';
        this.setProps.highlightsArr = [];

        this.setProps = this.setProps.bind(this);
    }

    setProps(newProps) {
        this.setState(newProps);
    }

    render() {
        return (
            <div>
                <DashPdfComponents
                    setProps={this.setProps}
                    {...this.state}
                />
            </div>
        )
    }
}

export default App;
